@use '_mixin' as m;

.breadcrumbs {
  padding: m.toRem(20) 0 m.toRem(25);
  display: flex;
  align-items: center;
  border-top: 1px solid var(--grey-border);
  // .breadcrumbs__list

  &__list {
    display: flex;
    align-items: center;
    gap: m.toRem(6);
    @media (max-width: m.toEm(768)) {
      gap: m.toRem(3);
    }
  }

  // .breadcrumbs__item

  &__item {
  }

  // .breadcrumbs__link

  &__link {
    position: relative;
    display: flex;
    align-items: center;
    gap: m.toRem(10);
    font-weight: 500;
    font-size: m.toRem(12);
    line-height: 200%;
    color: #637381;
    transition: color 0.3s ease 0s;
    @media (max-width: m.toEm(768)) {
      gap: m.toRem(6);
    }

    &::after {
      content: '';
      width: m.toRem(12);
      height: m.toRem(12);
      background: url('../img/svg/arrow-right.svg') 0 0 no-repeat;
    }

    @media (any-hover: hover) {
      &:hover {
        color: var(--main-color);
        .breadcrumbs__link-icon {
        }
      }
    }
  }

  // .breadcrumbs__link-icon

  &__link-icon {
    width: m.toRem(12);
    height: m.toRem(12);
    flex-shrink: 0;
    fill: var(--main-color);
    transform: translate(0px, m.toRem(-1));
    transition: fill 0.3s ease 0s;

    @media (any-hover: hover) {
      &:hover {
        fill: var(--black);
      }
    }
  }

  // .breadcrumbs__current

  &__current {
    font-weight: 500;
    font-size: m.toRem(12);
    line-height: 200%;
  }
}
