
@use '_mixin' as m;

.best {
  // .best__container

  &__container {
  }

  // .best__row

  &__row {
    &:not(:last-child) {
      margin-bottom: m.toRem(80);
    }

    @media (max-width: m.toEm(640)) {
      &:not(:last-child) {
        margin-bottom: m.toRem(50);
      }
    }
  }

  // .best__header

  &__header {
    margin-bottom: m.toRem(10);
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    justify-content: space-between;
    gap: m.toRem(10) m.toRem(20);
  }

  // .best__title

  &__title {
  }

  // .best__more

  &__more {
    padding: m.toRem(8) m.toRem(18);
    display: flex;
    align-items: center;
    justify-content: center;
    margin-left: auto;
    color: var(--green);
    border-radius: 50px;
    border: 1px solid var(--green);
    font-weight: 500;
    font-size: m.toRem(14);
    line-height: 171%;
    transition: background 0.3s ease, color 0.3s ease;

    @media (any-hover: hover) {
      &:hover {
        background-color: var(--green);
        color: var(--white);
      }
    }

    @media (max-width: m.toEm(640)) {
      padding: m.toRem(6) m.toRem(10);
    }
  }

  // .best__items

  &__items {
    display: grid;
    grid-template-columns: repeat(3, minmax(m.toRem(300), m.toRem(370)));
    gap: m.toRem(10);
    justify-content: space-between;

    @media (max-width: m.toEm(992)) {
      grid-template-columns: repeat(3, minmax(m.toRem(300), m.toRem(370)));
      overflow: auto;
      margin-left: m.toRem(-20);
      margin-right: m.toRem(-20);
      padding: m.toRem(1) m.toRem(20);

      &::-webkit-scrollbar {
        display: none;
      }
    }
  }

  &__item {
    @media (max-width: m.toEm(992)) {
      margin-top: m.toRem(2);
      margin-bottom: m.toRem(2);
    }
  }

  // .best__item

  &__link {
    padding: m.toRem(30) m.toRem(20);
    min-height: m.toRem(205);
    display: grid;
    align-items: start;
    gap: m.toRem(8);
    box-shadow: 0 1px 3px 0 rgba(166, 175, 195, 0.4);
    background: var(--white);
    border-radius: 12px;
    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis;
    transition: box-shadow 0.3s ease 0s;

    @media (any-hover: hover) {
      &:hover {
        box-shadow: 0 2px 4px 0 rgba(166, 175, 195, 1);
      }
    }

  }

  // .best__item-img

  &__item-img {
    width: m.toRem(60);
    height: m.toRem(60);
    border-radius: 6px;
    overflow: hidden;
    object-position: center;
    object-fit: cover;
  }

  // .best__item-info

  &__item-title {
    font-weight: 600;
    font-size: m.toRem(18);
    text-transform: capitalize;
    white-space: nowrap;
    overflow: hidden;
    line-height: 1.1;
    text-overflow: ellipsis;

    @media (max-width: m.toEm(1280)) {
      font-size: m.toRem(16);
    }
  }

  &__item-info {
    font-size: m.toRem(13);
    color: var(--text);
    white-space: nowrap;
    overflow: hidden;
    line-height: 1.1;
    text-overflow: ellipsis;
  }

  // .best__item-rating

  &__item-rating {
  }
}
