@use '_mixin' as m;

.reviews-card {
  padding: m.toRem(15) m.toRem(14) m.toRem(15) m.toRem(20);
  flex: 0 1 m.toRem(255);
  min-width: m.toRem(200);
  position: relative;
  border: 1px solid var(--grey-border);
  border-radius: 12px;
  @media (max-width: m.toEm(1280)) {
    padding: m.toRem(15);
  }

  // .reviews-card__link

  &__link {
    position: absolute;
    width: 100%;
    height: 100%;
    top: 0;
    left: 0;
  }
}
.review-card {
  // .review-card__date

  &__date {
    margin-bottom: m.toRem(10);
    font-size: m.toRem(12);
    line-height: 100%;
    text-transform: capitalize;
    color: var(--text);
  }

  // .review-card__info

  &__info {
    margin-bottom: m.toRem(10);
    display: flex;
    gap: m.toRem(10);
  }

  // .review-card__icon

  &__icon {
    width: m.toRem(48);
    height: m.toRem(48);
    flex-shrink: 0;
    border-radius: 6px;
    overflow: hidden;
    object-position: cover;
    object-fit: contain;
  }

  // .review-card__rating

  &__rating {
  }

  // .review-card__text

  &__text {
    margin: 0;
    font-size: m.toRem(12);
    line-height: 140%;
    color: var(--text);
    -webkit-line-clamp: 3;
    display: -webkit-box;
    -webkit-box-orient: vertical;
    overflow: hidden;
  }
}
