@use '_mixin' as m;

.options {
  display: flex;
  justify-items: start;
  background: var(--bg-tab);
  box-shadow: inset 0 0 0 1px var(--grey-border);
  border-radius: 5px;
  height: m.toRem(30);
  width: max-content;
  overflow: hidden;

  // .options__item
  &__item {
    flex: 0 0 m.toRem(52);
    position: relative;
    cursor: pointer;
    &:not(:last-child) {
      border-right: 1px solid var(--grey-border);
    }
    transition: background 0.3s ease, color 0.3s ease;
  }

  // .options__input
  &__input {
    width: 0;
    height: 0;
    opacity: 0;
    position: absolute;
    -webkit-appearance: none;
    -moz-appearance: none;
    appearance: none;

    &:checked + .options__label {
      color: var(--white);
      background: var(--green);
    }
  }

  // .options__label
  &__label {
    display: flex;
    align-items: center;
    cursor: pointer;
    font-size: m.toRem(13);
    padding: m.toRem(6) m.toRem(8);
    font-weight: 500;
    justify-content: center;
    height: 100%;
  }
}

.type-card {
  padding: m.toRem(10) m.toRem(0);
  display: flex;
  flex-direction: column;
  gap: m.toRem(16);
  align-items: flex-start;

  @media (max-width: m.toEm(768)) {
    padding: 0;
  }

  // .type-card__item

  &__item {
    position: relative;
    cursor: pointer;
  }

  // .type-card__input

  &__input {
    width: 0;
    height: 0;
    opacity: 0;
    position: absolute;
    -webkit-appearance: none;
    -moz-appearance: none;
    appearance: none;

    &:focus-visible + .type-card__label:before {
      outline-offset: 2px;
      outline: 1px solid var(--blue);
    }

    &:checked + .type-card__label:before {
      border: 1px solid var(--background-blue);
    }

    &:checked + .type-card__label:after {
      transform: scale(1);
    }
  }

  // .type-card__label

  &__label {
    display: inline-flex;
    align-items: center;
    gap: m.toRem(10);
    cursor: pointer;
    font-size: m.toRem(14);
    color: rgba(32, 34, 36, 0.8);

    &:before {
      content: '';
      align-self: flex-start;
      width: m.toRem(22);
      height: m.toRem(22);
      flex: 0 0 m.toRem(22);
      border-radius: 50%;
      background-color: var(--white);
      border: 1px solid #888;
    }

    &:after {
      content: '';
      position: absolute;
      left: m.toRem(6);
      top: m.toRem(6);
      width: m.toRem(10);
      height: m.toRem(10);
      border-radius: 50%;
      background: var(--background-blue);
      transition: transform 0.3s ease;
      transform: scale(0);
    }
  }
}

.options-lang {
  padding: m.toRem(10) m.toRem(0);
  display: flex;
  flex-direction: column;

  gap: m.toRem(16);
  width: 100%;

  @media (max-width: m.toEm(768)) {
    padding: 0;
  }

  &__item {
    position: relative;
    cursor: pointer;
  }

  &__input {
    width: 0;
    height: 0;
    opacity: 0;
    position: absolute;
    -webkit-appearance: none;
    -moz-appearance: none;
    appearance: none;

    &:focus-visible + .options-lang__label:before {
      outline-offset: 2px;
      outline: 1px solid var(--blue);
    }

    &:checked + .options-lang__label:before {
      border: 1px solid var(--background-blue);
    }

    &:checked + .options-lang__label:after {
      transform: scale(1);
    }
  }

  &__label {
    width: 100%;
    display: flex;
    align-items: center;
    justify-content: space-between;
    gap: m.toRem(10);
    height: m.toRem(35);
    cursor: pointer;
    font-size: m.toRem(14);
    color: #404040;

    &:after {
      content: '';

      width: m.toRem(18);
      height: m.toRem(13);
      border-radius: 50%;
      background: url('../img/header/check.svg') 0 0 no-repeat;
      transition: transform 0.3s ease;
      transform: scale(0);
    }
  }
}
