
@use '_mixin' as m;

.purchase {
  position: relative;
  z-index: 5;
  min-height: m.toRem(480);
  background-color: var(--green);
  display: grid;

  @media (max-width: m.toEm(640)) {
    min-height: m.toRem(430);
  }

  &__container {
    width: 100%;
  }

  // .purchase__container

  &__body {
    margin: 0 m.toRem(-100);
    padding: m.toRem(70) m.toRem(0);

    min-height: 100%;
    display: flex;
    justify-content: center;
    color: var(--white);
    position: relative;

    &::before {
      content: '';
      position: absolute;
      left: m.toRem(-30);
      top: 0;
      width: 28%;
      height: m.toRem(425);
      background: url('../img/svg/purchase_top.svg') left top / contain
        no-repeat;
      pointer-events: none;

      @media (max-width: m.toEm(640)) {
        left: 0;
        top: m.toRem(-20);
        width: 40%;
      }
    }

    &::after {
      content: '';
      position: absolute;
      right: m.toRem(-90);
      bottom: 0;
      width: 28%;
      height: m.toRem(420);
      background: url('../img/svg/purchase_bottom.svg') 100% bottom / contain
        no-repeat;
      pointer-events: none;

      @media (max-width: m.toEm(640)) {
        width: 40%;
        right: m.toRem(0);
      }
    }

    @media (max-width: m.toEm(1280)) {
      margin: 0 m.toRem(0);
    }
  }

  &__block {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    max-width: m.toRem(510);
  }

  // .purchase__label

  &__label {
    margin-bottom: m.toRem(12);
    font-weight: 500;
    line-height: 150%;
  }

  // .purchase__title

  &__title {
    margin-bottom: m.toRem(10);
    font-family: var(--second-family);
    font-weight: 600;
    font-size: m.toRem(50);
    line-height: 110%;
    text-align: center;

    @media (max-width: m.toEm(1024)) {
      font-size: m.toRem(34);
    }

    @media (max-width: m.toEm(640)) {
      font-size: m.toRem(32);
    }
  }

  // .purchase__text

  &__text {
    margin-bottom: m.toRem(30);
    line-height: 150%;
    text-align: center;

    @media (max-width: m.toEm(640)) {
      margin-bottom: m.toRem(20);
    }
  }

  // .purchase__link

  &__link {
    font-size: m.toRem(16);
    min-width: m.toRem(167);
    min-height: m.toRem(50);

    @media (max-width: m.toEm(640)) {
      min-height: m.toRem(45);
    }
  }
}
