@use '_mixin' as m;

.statistics {
  position: relative;
  z-index: 5;
  margin-top: m.toRem(-40);
  background-color: var(--green);

  &::after {
    content: '';
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background: url('../img/decor/statistics.svg') center bottom / cover
      no-repeat;

    @media (max-width: m.toEm(640)) {
      background: url('../img/decor/statistics_mob.svg') center top / cover
        no-repeat;
    }
  }

  // .statistics__container

  &__container {
  }

  // .statistics__list

  &__list {
    padding: m.toRem(52) m.toRem(20);
    min-height: m.toRem(200);
    display: flex;
    align-items: start;
    justify-content: space-between;
    gap: m.toRem(70);
    color: var(--white);

    @media (max-width: m.toEm(1280)) {
      padding: m.toRem(37) m.toRem(20);
      min-height: m.toRem(160);
    }

    @media (max-width: m.toEm(1024)) {
      padding: m.toRem(37) m.toRem(5);
      gap: m.toRem(40);
    }

    @media (max-width: m.toEm(640)) {
      margin: 0 auto;
      flex-direction: column;
      align-items: center;
      max-width: m.toRem(300);
    }
  }

  // .statistics__item

  &__item {
    flex: 0 1 m.toRem(325);
    display: flex;
    flex-direction: column;
    align-items: center;
    text-align: center;

    @media (max-width: m.toEm(640)) {
      flex: 0 1 auto;
    }
  }

  // .statistics__amount

  &__amount {
    margin-bottom: m.toRem(15);
    font-weight: 700;
    font-size: m.toRem(40);

    @media (max-width: m.toEm(1024)) {
      font-size: m.toRem(30);
    }
  }

  // .statistics__text

  &__text {
    line-height: 120%;
    font-size: m.toRem(18);

    @media (max-width: m.toEm(1024)) {
      font-size: m.toRem(16);
    }
  }
}
