@use '_mixin' as m;

.rating {
  display: flex;

  gap: m.toRem(5) m.toRem(10);
  align-items: center;
  justify-content: end;
  flex-wrap: wrap;

  @media (max-width: m.toEm(768)){
      font-size: m.toRem(14);
      justify-content: start;
  }
  

  &--green {
    .rating__item-icon {
      stroke: var(--green);
      &.filled {
        fill: var(--green);
      }
    }
  }

  &__items {
    display: flex;
    align-items: center;
    gap: m.toRem(4);
  }

  &__value {
    font-weight: 500;
    line-height: 150%;
  }

  // .rating__item

  &__item {
    font-size: 0;
  }

  // .rating__item-icon

  &__item-icon {
    width: m.toRem(16);
    height: m.toRem(16);
    fill: none;
    stroke: #f59e0b;
    stroke-width: m.toRem(1.5);

    &.filled {
      fill: #f59e0b;
    }
  }
}

.rating-group {
  &__items {
    display: flex;
    flex-direction: row-reverse;
    justify-content: center;
    gap: 3px;

    @media (max-width: 375px) {
      gap: 1px;
    }
  }

  &__item {
    opacity: 0;
    width: 0;
    cursor: pointer;

    &:checked ~ .rating-group__label {
      background: url('../img/svg/star-filled.svg') 0 0 / 25px no-repeat;
    }

    &:focus + .rating-group__label {
      background: url('../img/svg/star-filled.svg') 0 0 / 25px no-repeat;
    }
  }

  &__label {
    cursor: pointer;
    width: 25px;
    height: 25px;
    background: url('../img/svg/star.svg') 0 0 / 25px no-repeat;
  }
}
