@use '_mixin' as m;

.news {
  padding-bottom: 0;

  // .news__container

  &__container {
  }

  // .news__header

  &__header {
    margin-bottom: m.toRem(60);

    @media (max-width: m.toEm(640)) {
      margin-bottom: m.toRem(40);
    }
  }

  // .news__items

  &__items {
    display: grid;
    grid-template-columns: repeat(3, minmax(m.toRem(300), m.toRem(370)));
    gap: m.toRem(10);
    justify-content: space-between;

    @media (max-width: m.toEm(992)) {
      grid-template-columns: repeat(3, minmax(m.toRem(300), m.toRem(370)));

      overflow: auto;
      margin-left: m.toRem(-20);
      margin-right: m.toRem(-20);
      padding: m.toRem(0) m.toRem(20);

      &::-webkit-scrollbar {
        display: none;
      }
    }
  }

  // .news__item

  &__item {
    position: relative;
    display: flex;
    flex-direction: column;
    align-items: start;
    transition: transform 0.3s ease 0s;

    @media (any-hover: hover) {
      &:hover {
        transform: translate(0px, m.toRem(-5));
      }
    }
  }

  &__link {
    width: 100%;
    height: 100%;
    position: absolute;
    top: 0;
    left: 0;
  }

  // .news__item-img

  &__item-img {
    margin-bottom: m.toRem(30);
    width: 100%;
    aspect-ratio: 37 / 22;
    object-fit: cover;
    object-position: center;
    border-radius: 5px;

    @media (max-width: m.toEm(640)) {
      margin-bottom: m.toRem(20);
    }
  }

  // .news__item-date

  &__item-date {
    margin-bottom: m.toRem(25);
    padding: m.toRem(4) m.toRem(15);
    display: flex;
    align-items: center;
    justify-content: center;
    min-height: m.toRem(28);
    border-radius: 5px;
    background-color: var(--green);
    font-weight: 500;
    font-size: m.toRem(12);
    color: var(--white);

    @media (max-width: m.toEm(640)) {
      margin-bottom: m.toRem(18);
    }
  }

  // .news__item-title

  &__item-title {
    margin-bottom: m.toRem(12);
    font-weight: 600;
    font-size: m.toRem(24);
    line-height: 125%;
    display: -webkit-box;
    -webkit-line-clamp: 2;
    -webkit-box-orient: vertical;
    overflow: hidden;

    @media (max-width: m.toEm(992)) {
      font-size: m.toRem(22);
    }

    @media (max-width: m.toEm(640)) {
      margin-bottom: m.toRem(8);
      font-size: m.toRem(20);
    }
  }

  // .news__item-text

  &__item-text {
  }
}
