@use '_mixin' as m;

.reviews-categories {
  // .reviews-categories__container

  &__container {
    display: grid;
    gap: m.toRem(30);
    grid-template-columns: auto 1fr;
    @media (max-width: m.toEm(1280)) {
      gap: m.toRem(20);
    }
    @media (max-width: m.toEm(992)) {
      grid-template-columns: 1fr;
    }
  }

  // .reviews-categories__filter

  &__filter {
  }

  // .reviews-categories__body

  &__body {
    overflow: auto;
  }

  // .reviews-categories__header

  &__header {
    padding: m.toRem(1) m.toRem(1);
    margin-bottom: m.toRem(16);
    display: flex;
    align-items: center;
    justify-content: space-between;
    gap: m.toRem(15);

    @media (max-width: m.toEm(992)) {
      flex-direction: column-reverse;
      align-items: stretch;
    }
  }
  &__header-results {
    font-size: m.toRem(14);
    color: var(--text);
    @media (max-width: m.toEm(992)) {
      width: 100%;
    }
  }

  // .reviews-categories__header-filters

  &__header-filters {
    display: flex;
    align-items: center;
    justify-content: space-between;
    gap: m.toRem(15);
  }

  // .reviews-categories__header-filters-open

  &__header-filters-open {
    display: none;
    @media (max-width: m.toEm(992)) {
      padding: m.toRem(5) m.toRem(15);
      min-height: m.toRem(40);
      flex-shrink: 0;
      display: block;
    }
  }

  &__header-select {
    margin-left: auto;
    width: m.toRem(190);
  }

  // .reviews-categories__items

  &__items {
    &:not(:last-child) {
      margin-bottom: m.toRem(30);
    }
    padding: m.toRem(1) m.toRem(1) m.toRem(2);
    display: flex;
    flex-direction: column;
    gap: m.toRem(20);
    @media (max-width: m.toEm(992)) {
      gap: m.toRem(15);
    }
  }
}
