@use '_mixin' as m;

.text-block {
  margin: 0 auto;
  display: flex;
  flex-direction: column;
  align-items: center;
  max-width: m.toRem(520);

  // .text-block__label

  &__label {
    margin-bottom: m.toRem(10);
    font-weight: 600;
    font-size: m.toRem(18);
    text-align: center;
    color: var(--green);

    @media (max-width: m.toEm(640)) {
      font-size: m.toRem(17);
    }
  }

  // .text-block__title

  &__title {
    &:not(:last-child) {
      margin-bottom: m.toRem(12);
    }

    font-weight: 700;
    font-size: m.toRem(40);
    line-height: 120%;
    text-align: center;

    @media (max-width: m.toEm(1024)) {
      font-size: m.toRem(34);
    }

    @media (max-width: m.toEm(640)) {
      font-size: m.toRem(32);
    }
  }

  // .text-block__text

  &__text {
    line-height: 150%;
    text-align: center;
    color: var(--text);
    max-width: m.toRem(500);
  }
}

.text-block > * {
  transform: translate(0px, 1.875rem);
  opacity: 0;
}

.text-block > *.loaded {
  transform: translate(0px, 0);
  opacity: 1;
  transition: transform 1s ease, opacity 1s ease;
}

.text-block__label.loaded {
  transition-delay: 0.1s;
}

.text-block__title.loaded {
  transition-delay: 0.3s;
}

.text-block__text.loaded {
  transition-delay: 0.5s;
}

.loaded .hero__search {
  transition-delay: 0.9s;
}

.header-main > *,
.hero__search {
  transform: translate(0px, 1.875rem);
  opacity: 0;
}

.loaded .header-main > *,
.loaded .hero__search {
  transform: translate(0px, 0);
  opacity: 1;
  transition: transform 1s ease, opacity 1s ease;
}

.loaded .header-main__label {
  transition-delay: 0.1s;
}

.loaded .header-main__title {
  transition-delay: 0.3s;
}

.loaded .header-main__text {
  transition-delay: 0.5s;
}

.loaded .hero__search {
  transition-delay: 0.7s;
}

.header-main {
  max-width: m.toRem(800);
  margin: 0 auto;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  text-align: center;

  &--page {
    padding: m.toRem(25) m.toRem(0) m.toRem(50);
    width: 100%;
    @media (max-width: m.toEm(1280)) {
      padding: m.toRem(15) m.toRem(0) m.toRem(35);
    }
    @media (max-width: m.toEm(768)) {
      padding: m.toRem(10) m.toRem(0) m.toRem(25);
    }
  }

  @media (max-width: m.toEm(1280)) {
    max-width: m.toRem(700);
  }

  @media (max-width: m.toEm(992)) {
    max-width: m.toRem(500);
  }

  @media (max-width: m.toEm(640)) {
    max-width: m.toRem(320);
  }

  // .hero__label

  &__label {
    margin-bottom: m.toRem(28);
    font-weight: 700;
    font-size: m.toRem(14);
    color: var(--green);

    @media (max-width: m.toEm(1536)) {
      margin-bottom: m.toRem(20);
    }

    @media (max-width: m.toEm(640)) {
      margin-bottom: m.toRem(15);
    }
  }

  // .hero__title

  &__title {
    &:not(:last-child) {
      margin-bottom: m.toRem(16);
    }

    font-family: var(--second-family);
    font-weight: 600;
    font-size: m.toRem(60);
    line-height: 97%;

    @media (max-width: m.toEm(1536)) {
      font-size: m.toRem(50);
    }

    @media (max-width: m.toEm(1280)) {
      font-size: m.toRem(40);
    }

    @media (max-width: m.toEm(992)) {
      font-size: m.toRem(36);
    }

    @media (max-width: m.toEm(640)) {
      &:not(:last-child) {
        margin-bottom: m.toRem(12);
      }

      font-size: m.toRem(30);
    }
  }

  // .hero__text

  &__text {
  }
}

.text {
  font-weight: 400;
  font-size: m.toRem(16);
  line-height: 150%;
  // text-align: center;
  color: var(--text);

  @media (max-width: m.toEm(640)) {
    font-size: m.toRem(14);
  }
}

.section-offset {
  padding-top: m.toRem(50);
  padding-bottom: m.toRem(50);

  @media (max-width: m.toEm(640)) {
    padding-top: m.toRem(40);
    padding-bottom: m.toRem(40);
  }
}

.section-offset-top {
  padding-top: m.toRem(100);
  padding-bottom: m.toRem(50);

  @media (max-width: m.toEm(640)) {
    padding-top: m.toRem(80);
    padding-bottom: m.toRem(40);
  }
}

.section-offset-bottom {
  padding-top: m.toRem(50);
  padding-bottom: m.toRem(100);

  @media (max-width: m.toEm(640)) {
    padding-top: m.toRem(40);
    padding-bottom: m.toRem(80);
  }
}

.title {
  font-family: var(--second-family);
  font-weight: 600;
  font-size: m.toRem(32);
  transform: translate(0px, m.toRem(20));
  opacity: 0;

  @media (max-width: m.toEm(1024)) {
    font-size: m.toRem(28);
  }

  @media (max-width: m.toEm(640)) {
    font-size: m.toRem(22);
  }
}

.title.loaded {
  transform: translate(0px, 0);
  opacity: 1;
}

.title.loaded {
  transition: transform 1s ease, opacity 1s ease;
}

.skeleton-card {
  position: relative;
  width: 100%;
  height: m.toRem(205);
  border-radius: 12px;
  overflow: hidden;
  box-shadow: 0 1px 3px 0 rgba(166, 175, 195, 0.4);
  user-select: none;

  &--company {
    height: m.toRem(244);
  }

  img {
    width: 100%;
    height: 100%;
    // object-fit: contain;
  }
}
.skeleton-card::after {
  content: '';
  position: absolute;
  top: 0;
  left: -150%;
  width: 200%;
  height: 100%;
  background: linear-gradient(
    90deg,
    rgba(255, 255, 255, 0) 0%,
    rgba(255, 255, 255, 0.3) 45%,
    rgba(255, 255, 255, 0.6) 50%,
    rgba(255, 255, 255, 0.3) 55%,
    rgba(255, 255, 255, 0) 100%
  );
  animation: shimmer 2.2s ease-in-out infinite;
}

@keyframes shimmer {
  0% {
    left: -150%;
  }
  100% {
    left: 100%;
  }
}

.link {
  &:focus-visible {
    outline: 2px dotted var(--green);
    outline-offset: 2px;
  }
}
