@use '_mixin' as m;

body {
  background: var(--white);

  &._lock {
    overflow: hidden;
    touch-action: none;
    overscroll-behavior: none;
  }

  // Сайт замонтирован
  .loaded & {
  }
}

.wrapper {
  min-height: 100%;
  display: flex;
  flex-direction: column;
  overflow: hidden;

  @supports (overflow: clip) {
    overflow: clip;
  }

  // Прижимаем footer
  > main {
    flex: 1 1 auto;
  }

  // фикс для слайдеров
  > * {
    min-width: 0;
  }
}

[class*='__container'] {
  margin: 0 auto;
  padding: 0 var(--container-offset);
  max-width: var(--container-width);
  width: 100%;
}

.page-top {
  background: var(--bg-gradient);
}

.page {
  padding-top: m.toRem(40);

  @media (max-width: m.toEm(992)){
        padding-top: m.toRem(15);
  }
}


