@use '_mixin' as m;

.categories {
  // .categories__container

  &__container {
  }

  // .categories__title

  &__title {
    margin-bottom: m.toRem(16);
  }

  // .categories__list

  &__list {
    display: grid;
    grid-template-columns: repeat(6, minmax(m.toRem(150), m.toRem(170)));
    gap: m.toRem(10);
    justify-content: space-between;

    @media (max-width: m.toEm(992)) {
      overflow: auto;
      margin-left: m.toRem(-20);
      margin-right: m.toRem(-20);
      padding: m.toRem(0) m.toRem(20);

      &::-webkit-scrollbar {
        display: none;
      }
    }
  }

  // .categories__item

  &__item {
    position: relative;
    padding: m.toRem(30) m.toRem(30) m.toRem(30) m.toRem(20);
    border: 1px solid var(--main-color);
    border-radius: 12px;
    aspect-ratio: 17 / 20;
    display: flex;
    flex-direction: column;
    align-items: start;
    justify-content: end;
    gap: m.toRem(10);
    transition: transform 0.3s ease 0s;

    @media (any-hover: hover) {
      &:hover {
        transform: translate(0px, m.toRem(-5));
      }
    }

    @media (max-width: m.toEm(1280)) {
      padding: m.toRem(30) m.toRem(20) m.toRem(20);
    }

    // min-height: m.toRem(200);
  }

  &__link {
    position: absolute;
    width: 100%;
    height: 100%;
    top: 0;
    left: 0;
  }

  // .categories__icon

  &__icon {
    width: m.toRem(48);
    height: m.toRem(48);
  }

  // .categories__text

  &__text {
    font-weight: 600;
    font-size: m.toRem(18);
    text-transform: capitalize;

    @media (max-width: m.toEm(1280)) {
      font-size: m.toRem(16);
    }
  }
}
