@use '_mixin' as m;

.advantages {
  // .advantages__container

  &__container {
  }

  // .advantages__header

  &__header {
    margin-bottom: m.toRem(60);

    @media (max-width: m.toEm(640)) {
      margin-bottom: m.toRem(40);
    }
  }

  // .advantages__items

  &__items {
    display: grid;
    grid-template-columns: repeat(auto-fill, minmax(m.toRem(350), 1fr));
    gap: m.toRem(35) m.toRem(30);

    @media (max-width: m.toEm(1024)) {
      gap: m.toRem(10);
    }

    @media (max-width: m.toEm(640)) {
      grid-template-columns: repeat(auto-fill, minmax(m.toRem(290), 1fr));
    }
  }

  // .advantages__item

  &__item {
    padding: m.toRem(40) m.toRem(30) m.toRem(40) m.toRem(40);
    display: grid;
    grid-template-rows: m.toRem(70) m.toRem(70) auto;
    border-radius: 20px;
    box-shadow: 0 5px 12px 0 rgba(0, 0, 0, 0.1);
    background: var(--white);

    @media (max-width: m.toEm(1280)) {
      padding: m.toRem(20) m.toRem(20);
    }

    @media (max-width: m.toEm(640)) {
      grid-template-rows: m.toRem(70) m.toRem(55) auto;
    }
  }

  // .advantages__item-img

  &__item-img {
    // margin-bottom: m.toRem(30);
    width: m.toRem(70);
    height: m.toRem(70);
    display: flex;
    align-items: center;
    justify-content: center;
    border-radius: 15px;
    box-shadow: 0 4px 11px 0 rgba(0, 0, 0, 0.06);
    background: var(--green);
  }

  // .advantages__item-icon

  &__item-icon {
    width: m.toRem(36);
    height: m.toRem(36);
  }

  // .advantages__item-title

  &__item-title {
    margin-bottom: m.toRem(8);
    align-self: end;
    font-weight: 600;
    font-size: m.toRem(24);
    line-height: 125%;
    display: -webkit-box;
    -webkit-line-clamp: 2; // количество строк
    -webkit-box-orient: vertical;
    overflow: hidden;

    @media (max-width: m.toEm(992)) {
      font-size: m.toRem(22);
      line-height: 110%;
    }

    @media (max-width: m.toEm(640)) {
      font-size: m.toRem(20);
      line-height: 110%;
    }
  }

  // .advantages__item-text

  &__item-text {
    padding-top: m.toRem(5);
  }
}
