//

@use '_mixin' as m;

.footer {
  padding-top: m.toRem(120);
  padding-bottom: m.toRem(110);
  display: grid;

  background-color: var(--white);
  min-height: m.toRem(450);

  @media (max-width: m.toEm(1440)) {
    padding-top: m.toRem(80);
    padding-bottom: m.toRem(100);
    min-height: auto;
  }

  @media (max-width: m.toEm(768)) {
    padding-top: m.toRem(50);
    padding-bottom: m.toRem(40);
  }

  // .footer__container

  &__container {
    width: 100%;
    display: grid;
    grid-template-columns: m.toRem(270) auto auto;
    gap: m.toRem(30);

    @media (max-width: m.toEm(1280)) {
      grid-template-columns: m.toRem(270) auto;
      align-items: start;
    }

    @media (max-width: m.toEm(992)) {
      grid-template-columns: 1fr;
      gap: m.toRem(50);
    }
  }

  // .footer__info

  &__info {
    max-width: m.toRem(240);
    display: flex;
    flex-direction: column;
    gap: m.toRem(20);
  }

  &__logo {
    flex-shrink: 0;
    position: relative;
    z-index: 5;
    width: m.toRem(184);
    height: m.toRem(39);

    @media (max-width: m.toEm(640)) {
      width: m.toRem(150);
    }
  }

  &__logo-icon {
    width: 100%;
    height: 100%;
  }

  // .footer__text

  &__text {
    font-size: m.toRem(14);
  }

  &__tel {
    position: relative;
    display: flex;
    align-items: start;
    gap: m.toRem(10);
    font-weight: 500;
    font-size: m.toRem(14);
    line-height: 157%;

    &::before {
      content: '';
      width: m.toRem(20);
      height: m.toRem(20);
      background: url('../img/svg/phone.svg') 0 0 no-repeat;
    }
  }

  &__socials {
    display: flex;
    flex-direction: column;
    gap: m.toRem(25);

    justify-self: end;

    @media (max-width: m.toEm(1280)) {
      justify-self: start;
    }
    @media (max-width: m.toEm(768)){
        gap: m.toRem(18);
    }
  }

  &__socials-title {
    margin-bottom: m.toRem(10);
    font-weight: 600;
    line-height: 162%;
    @media (max-width: m.toEm(768)){
        margin-bottom: 0;
    }
  }

  // .footer__socials

  &__socials-items {
    display: flex;
    align-items: center;
    gap: m.toRem(15);

    @media (max-width: m.toEm(768)) {
      gap: m.toRem(20);
    }
  }

  // .footer__socials-item

  &__socials-item {
    flex-shrink: 0;
    width: m.toRem(32);
    height: m.toRem(32);

    display: flex;
    align-items: center;
    justify-content: center;

    border-radius: 50%;
    border: 1px solid var(--grey-border);
    transition: border 0.3s ease, background 0.3s ease;

    &:focus-visible {
      outline: 2px solid var(--green);
    }

    @media (any-hover: hover) {
      &:hover {
        background-color: var(--green);
        border: 1px solid var(--green);

        .footer__socials-icon {
          fill: var(--white);
        }
      }
    }

    @media (max-width: m.toEm(768)) {
      width: m.toRem(40);
      height: m.toRem(40);
    }
  }

  // .footer__socials-icon

  &__socials-icon {
    width: m.toRem(16);
    height: m.toRem(16);
    fill: var(--main-color);
    transition: fill 0.3s ease;

    &:active {
      fill: var(--blue);
    }

    @media (max-width: m.toEm(768)) {
      width: m.toRem(20);
      height: m.toRem(20);
    }
  }

  &__copy {
  }
}

.menu-footer {
  display: grid;
  gap: m.toRem(70);
  grid-template-columns: repeat(3, auto);
  justify-content: center;

  @media (max-width: m.toEm(992)) {
    grid-template-columns: repeat(3, auto);
    justify-self: start;
    justify-content: start;
    gap: m.toRem(120);
  }

  @media (max-width: m.toEm(768)) {
    gap: m.toRem(30);
  }

  @media (max-width: m.toEm(768)) {
    grid-template-columns: 1fr;
    gap: m.toRem(15);
    justify-self: stretch;
  }

  // .menu-footer__column

  &__column {
  }

  // .menu-footer__title

  &__title {
    position: relative;
    margin-bottom: m.toRem(32);
    font-size: m.toRem(16);
    line-height: 162%;
    font-weight: 600;

    @media (max-width: m.toEm(768)) {
      margin-bottom: m.toRem(10);
      width: 100%;
      display: flex;
      align-items: center;
      justify-content: space-between;
      font-size: m.toRem(20);
    }

    ._init & {
      &::after {
        content: '';
        background: url('../img/svg/arrow.svg') 0 0 / contain no-repeat;
        width: m.toRem(20);
        height: m.toRem(16);
        transition: transform 0.3s ease;
      }

      &._active {
        &::after {
          transform: rotate(-180deg);
        }
      }
    }
  }

  // .menu-footer__list

  &__list {
  }

  // .menu-footer__item

  &__item {
    &:not(:last-child) {
      margin-bottom: m.toRem(12);
    }
    font-size: m.toRem(14);
    line-height: 171%;
    color: var(--text);

    @media (max-width: m.toEm(768)) {
      font-size: m.toRem(16);
    }
  }

  // .menu-footer__link

  &__link {
    transition: color 0.3s ease;

    &:focus-visible {
      outline: 2px solid var(--green);
    }

    @media (any-hover: hover) {
      &:hover {
        color: var(--green);
      }
    }
  }
}
