@use '_mixin' as m;

.block-company {
  padding: m.toRem(0) m.toRem(30) m.toRem(55);

  position: relative;
  border-radius: 12px;
  box-shadow: 0 1px 3px 0 rgba(166, 175, 195, 0.4);
  background: var(--white);
  @media (max-width: m.toEm(1280)) {
    padding: m.toRem(0) m.toRem(20) m.toRem(45);
  }
  @media (max-width: m.toEm(768)) {
    padding: m.toRem(0) m.toRem(15) m.toRem(45);
  }

  // .block-company__header

  &__link {
    margin-left: m.toRem(-30);
    margin-right: m.toRem(-30);
    padding: m.toRem(20) m.toRem(30) m.toRem(25);
    display: flex;
    align-items: center;
    justify-content: space-between;
    gap: m.toRem(15);
    border-bottom: 1px solid var(--grey-border);
    position: relative;
    @media (max-width: m.toEm(1280)) {
      margin-left: m.toRem(-20);
      margin-right: m.toRem(-20);
      padding: m.toRem(20);
    }
    @media (max-width: m.toEm(768)) {
      margin-left: m.toRem(-15);
      margin-right: m.toRem(-15);
      padding: m.toRem(15);
      flex-wrap: wrap;
      justify-content: start;
    }
  }

  // .block-company__logo

  &__logo {
    flex-shrink: 0;
    width: m.toRem(80);
    object-position: center;
    object-fit: contain;
    @media (max-width: m.toEm(992)) {
      width: m.toRem(60);
    }
    @media (max-width: m.toEm(768)) {
      width: m.toRem(40);
    }
  }

  // .block-company__info

  &__info {
    flex: 1 1 auto;
    align-items: start;
    flex-direction: column;
    gap: m.toRem(1);
    overflow: hidden;
  }

  // .block-company__name

  &__name {
    font-weight: 700;
    font-size: m.toRem(20);
    line-height: 120%;
    overflow: hidden;
    text-overflow: ellipsis;

    @media (max-width: m.toEm(992)) {
      font-size: m.toRem(18);
    }
    @media (max-width: m.toEm(768)) {
      font-size: m.toRem(16);
    }
  }

  // .block-company__email

  &__email {
    font-size: m.toRem(14);
    line-height: 157%;
    color: var(--text);
  }

  // .block-company__rating

  &__rating {
    align-self: start;
    @media (max-width: m.toEm(768)) {
      width: 100%;
    }
  }

  // .block-company__description

  &__description {
    padding-top: m.toRem(18);
    font-size: m.toRem(14);
    line-height: 171%;
    color: var(--text);
    @media (max-width: m.toEm(992)) {
      line-height: 150%;
    }
  }

  // .block-company__reviews

  &__reviews {
  }

  // .block-company__reviews-open

  &__reviews-open {
    position: absolute;
    bottom: m.toRem(20);
    right: m.toRem(30);
    display: flex;
    align-items: center;
    gap: m.toRem(6);
    font-size: m.toRem(14);
    line-height: 171%;
    text-align: right;
    color: var(--green);
    z-index: 2;
    transition: color 0.3s ease 0s;
    &::after {
      content: '';
      flex-shrink: 0;
      width: m.toRem(12);
      height: m.toRem(12);
      background: url('../img/svg/arrow-faq.svg') center / contain no-repeat;
      transition: transform 0.3s ease;
    }

    &._active {
      &::after {
        transform: rotate(-180deg);
      }
    }

    @media (any-hover: hover) {
      &:hover {
        color: var(--green-transition);
      }
    }
    @media (max-width: m.toEm(1280)) {
      right: m.toRem(20);
    }
    @media (max-width: m.toEm(768)) {
      right: m.toRem(15);
      bottom: m.toRem(10);
    }
  }

  // .block-company__reviews-items

  &__reviews-items {
    padding: m.toRem(20) m.toRem(30);
    margin: 0 m.toRem(-30);
    display: flex;
    gap: m.toRem(20);
    overflow: auto;
    @media (max-width: m.toEm(1280)) {
      padding: m.toRem(20) m.toRem(20);
      margin: 0 m.toRem(-20);
    }
    @media (max-width: m.toEm(768)) {
      gap: m.toRem(10);
      padding: m.toRem(20) m.toRem(15);
      margin: 0 m.toRem(-15);
    }
  }
}
