@use '_mixin' as m;

.pagination {
  margin: 0 auto;
  display: flex;
  align-items: center;
  justify-content: center;
  border: 1px solid var(--grey-border);
  border-radius: 6px;
  width: max-content;

  &__btn {
    min-width: m.toRem(35);
    height: m.toRem(35);
    display: flex;
    align-items: center;
    justify-content: center;
    text-align: right;
    transition: transition 0.3s ease 0s;

    &:active {
      transform: scale(0.9);
    }
  }
  
  &__btn-icon {
    width: m.toRem(12);
    height: m.toRem(8);

    &--prev {
      transform: rotate(90deg);
    }
    &--next {
      transform: rotate(-90deg);
    }
  }

  &__item {
    min-width: m.toRem(40);
    height: m.toRem(35);
    cursor: pointer;
    display: flex;
    align-items: center;
    justify-content: center;
    font-size: m.toRem(16);
    border-left: 1px solid var(--grey-border);

    &:last-of-type {
      border-right: 1px solid var(--grey-border);
    }
    transition: border 0.3s;

    @media (any-hover: hover) {
      &:hover {
        color: var(--green);
        background-color: var(--green_05);
        border: 1px solid var(--green);
      }
    }

    &.active {
      color: var(--green);
      background-color: var(--green_05);
      border: 1px solid var(--green);
    }
  }
}
