@use '_mixin' as m;

.button {
  padding: m.toRem(7);
  display: inline-flex;
  align-items: center;
  justify-content: center;
  gap: m.toRem(6);
  border-radius: 6px;
  color: var(--white);
  font-weight: 500;
  background-color: var(--green);
  font-size: m.toRem(14);
  transition: background 0.3s ease;

  &--transparent {
    background-color: transparent;
    color: var(--main-color);

    transition: background 0.3s ease, color 0.3s ease;

    @media (any-hover: hover) {
      &:hover {
        background-color: var(--green-transition);
        color: var(--white);
      }
    }
  }

  &--white {
    background-color: var(--white);
    color: var(--green);
    box-shadow: 0 1px 3px 0 rgba(166, 175, 195, 0.4);

    transition: background 0.3s ease, color 0.3s ease;

    @media (any-hover: hover) {
      &:hover {
        background-color: var(--green-transition);
        color: var(--white);
      }
    }
  }

  &:focus-visible {
    outline: 2px solid var(--green-transition);
  }

  &:active {
    outline: none;
  }

  @media (any-hover: hover) {
    &:hover {
      background-color: var(--green-transition);
    }
  }

  &__icon {
    width: m.toRem(16);
    height: m.toRem(16);
    fill: var(--white);
  }
}
