@font-face {
  font-family: "Inter";
  src: url("../fonts/Inter-Medium.woff2") format("woff2");
  font-weight: 500;
  font-display: swap;
  font-style: normal;
}
@font-face {
  font-family: "Inter";
  src: url("../fonts/Inter-Regular.woff2") format("woff2");
  font-weight: 400;
  font-display: swap;
  font-style: normal;
}
@font-face {
  font-family: "Inter";
  src: url("../fonts/Inter-Bold.woff2") format("woff2");
  font-weight: 700;
  font-display: swap;
  font-style: normal;
}
@font-face {
  font-family: "Inter";
  src: url("../fonts/Inter-SemiBold.woff2") format("woff2");
  font-weight: 600;
  font-display: swap;
  font-style: normal;
}
@font-face {
  font-family: "FONTSPRING DEMO - Gelica SBd";
  src: url("../fonts/Fontspring-DEMO-gelica-semibold.woff2") format("woff2");
  font-weight: 600;
  font-display: swap;
  font-style: normal;
}
:root {
  --font-family: "Inter", sans-serif;
  --second-family: "FONTSPRING DEMO - Gelica SBd", sans-serif;
  --content-width: 1170px;
  --container-offset: 15px;
  --container-width: calc(var(--content-width) + (var(--container-offset) * 2));
  --main-color: #111928;
  --white: #fff;
  --black: #000;
  --green: #13c296;
  --green-transition: #20a785;
  --text: #637381;
  --green_05: rgba(19, 194, 150, 0.05);
  --grey-light: #9ca3af;
  --grey-border: #dfe4ea;
  --grey-hover: #f3f4f6;
  --bg-gradient: linear-gradient(190deg, #fafafa 0%, #f6f9ff 100%);
  --bg-tab: #f3f4f6;
}

body {
  background: var(--white);
}
body._lock {
  overflow: hidden;
  touch-action: none;
  overscroll-behavior: none;
}
.wrapper {
  min-height: 100%;
  display: flex;
  flex-direction: column;
  overflow: hidden;
}
@supports (overflow: clip) {
  .wrapper {
    overflow: clip;
  }
}
.wrapper > main {
  flex: 1 1 auto;
}
.wrapper > * {
  min-width: 0;
}

[class*=__container] {
  margin: 0 auto;
  padding: 0 var(--container-offset);
  max-width: var(--container-width);
  width: 100%;
}

.page-top {
  background: var(--bg-gradient);
}

.page {
  padding-top: 2.5rem;
}
@media (max-width: 62em) {
  .page {
    padding-top: 0.9375rem;
  }
}

.text-block {
  margin: 0 auto;
  display: flex;
  flex-direction: column;
  align-items: center;
  max-width: 32.5rem;
}
.text-block__label {
  margin-bottom: 0.625rem;
  font-weight: 600;
  font-size: 1.125rem;
  text-align: center;
  color: var(--green);
}
@media (max-width: 40em) {
  .text-block__label {
    font-size: 1.0625rem;
  }
}
.text-block__title {
  font-weight: 700;
  font-size: 2.5rem;
  line-height: 120%;
  text-align: center;
}
.text-block__title:not(:last-child) {
  margin-bottom: 0.75rem;
}
@media (max-width: 64em) {
  .text-block__title {
    font-size: 2.125rem;
  }
}
@media (max-width: 40em) {
  .text-block__title {
    font-size: 2rem;
  }
}
.text-block__text {
  line-height: 150%;
  text-align: center;
  color: var(--text);
  max-width: 31.25rem;
}

.text-block > * {
  transform: translate(0px, 1.875rem);
  opacity: 0;
}

.text-block > *.loaded {
  transform: translate(0px, 0);
  opacity: 1;
  transition: transform 1s ease, opacity 1s ease;
}

.text-block__label.loaded {
  transition-delay: 0.1s;
}

.text-block__title.loaded {
  transition-delay: 0.3s;
}

.text-block__text.loaded {
  transition-delay: 0.5s;
}

.loaded .hero__search {
  transition-delay: 0.9s;
}

.header-main > *,
.hero__search {
  transform: translate(0px, 1.875rem);
  opacity: 0;
}

.loaded .header-main > *,
.loaded .hero__search {
  transform: translate(0px, 0);
  opacity: 1;
  transition: transform 1s ease, opacity 1s ease;
}

.loaded .header-main__label {
  transition-delay: 0.1s;
}

.loaded .header-main__title {
  transition-delay: 0.3s;
}

.loaded .header-main__text {
  transition-delay: 0.5s;
}

.loaded .hero__search {
  transition-delay: 0.7s;
}

.header-main {
  max-width: 50rem;
  margin: 0 auto;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  text-align: center;
}
.header-main--page {
  padding: 1.5625rem 0rem 3.125rem;
  width: 100%;
}
@media (max-width: 80em) {
  .header-main--page {
    padding: 0.9375rem 0rem 2.1875rem;
  }
}
@media (max-width: 48em) {
  .header-main--page {
    padding: 0.625rem 0rem 1.5625rem;
  }
}
@media (max-width: 80em) {
  .header-main {
    max-width: 43.75rem;
  }
}
@media (max-width: 62em) {
  .header-main {
    max-width: 31.25rem;
  }
}
@media (max-width: 40em) {
  .header-main {
    max-width: 20rem;
  }
}
.header-main__label {
  margin-bottom: 1.75rem;
  font-weight: 700;
  font-size: 0.875rem;
  color: var(--green);
}
@media (max-width: 96em) {
  .header-main__label {
    margin-bottom: 1.25rem;
  }
}
@media (max-width: 40em) {
  .header-main__label {
    margin-bottom: 0.9375rem;
  }
}
.header-main__title {
  font-family: var(--second-family);
  font-weight: 600;
  font-size: 3.75rem;
  line-height: 97%;
}
.header-main__title:not(:last-child) {
  margin-bottom: 1rem;
}
@media (max-width: 96em) {
  .header-main__title {
    font-size: 3.125rem;
  }
}
@media (max-width: 80em) {
  .header-main__title {
    font-size: 2.5rem;
  }
}
@media (max-width: 62em) {
  .header-main__title {
    font-size: 2.25rem;
  }
}
@media (max-width: 40em) {
  .header-main__title {
    font-size: 1.875rem;
  }
  .header-main__title:not(:last-child) {
    margin-bottom: 0.75rem;
  }
}
.text {
  font-weight: 400;
  font-size: 1rem;
  line-height: 150%;
  color: var(--text);
}
@media (max-width: 40em) {
  .text {
    font-size: 0.875rem;
  }
}

.section-offset {
  padding-top: 3.125rem;
  padding-bottom: 3.125rem;
}
@media (max-width: 40em) {
  .section-offset {
    padding-top: 2.5rem;
    padding-bottom: 2.5rem;
  }
}

.section-offset-top {
  padding-top: 6.25rem;
  padding-bottom: 3.125rem;
}
@media (max-width: 40em) {
  .section-offset-top {
    padding-top: 5rem;
    padding-bottom: 2.5rem;
  }
}

.section-offset-bottom {
  padding-top: 3.125rem;
  padding-bottom: 6.25rem;
}
@media (max-width: 40em) {
  .section-offset-bottom {
    padding-top: 2.5rem;
    padding-bottom: 5rem;
  }
}

.title {
  font-family: var(--second-family);
  font-weight: 600;
  font-size: 2rem;
  transform: translate(0px, 1.25rem);
  opacity: 0;
}
@media (max-width: 64em) {
  .title {
    font-size: 1.75rem;
  }
}
@media (max-width: 40em) {
  .title {
    font-size: 1.375rem;
  }
}

.title.loaded {
  transform: translate(0px, 0);
  opacity: 1;
}

.title.loaded {
  transition: transform 1s ease, opacity 1s ease;
}

.skeleton-card {
  position: relative;
  width: 100%;
  height: 12.8125rem;
  border-radius: 12px;
  overflow: hidden;
  box-shadow: 0 1px 3px 0 rgba(166, 175, 195, 0.4);
  user-select: none;
}
.skeleton-card--company {
  height: 15.25rem;
}
.skeleton-card img {
  width: 100%;
  height: 100%;
}

.skeleton-card::after {
  content: "";
  position: absolute;
  top: 0;
  left: -150%;
  width: 200%;
  height: 100%;
  background: linear-gradient(90deg, rgba(255, 255, 255, 0) 0%, rgba(255, 255, 255, 0.3) 45%, rgba(255, 255, 255, 0.6) 50%, rgba(255, 255, 255, 0.3) 55%, rgba(255, 255, 255, 0) 100%);
  animation: shimmer 2.2s ease-in-out infinite;
}

@keyframes shimmer {
  0% {
    left: -150%;
  }
  100% {
    left: 100%;
  }
}
.link:focus-visible {
  outline: 2px dotted var(--green);
  outline-offset: 2px;
}

.button {
  padding: 0.4375rem;
  display: inline-flex;
  align-items: center;
  justify-content: center;
  gap: 0.375rem;
  border-radius: 6px;
  color: var(--white);
  font-weight: 500;
  background-color: var(--green);
  font-size: 0.875rem;
  transition: background 0.3s ease;
}
.button--transparent {
  background-color: transparent;
  color: var(--main-color);
  transition: background 0.3s ease, color 0.3s ease;
}
@media (any-hover: hover) {
  .button--transparent:hover {
    background-color: var(--green-transition);
    color: var(--white);
  }
}
.button--white {
  background-color: var(--white);
  color: var(--green);
  box-shadow: 0 1px 3px 0 rgba(166, 175, 195, 0.4);
  transition: background 0.3s ease, color 0.3s ease;
}
@media (any-hover: hover) {
  .button--white:hover {
    background-color: var(--green-transition);
    color: var(--white);
  }
}
.button:focus-visible {
  outline: 2px solid var(--green-transition);
}
.button:active {
  outline: none;
}
@media (any-hover: hover) {
  .button:hover {
    background-color: var(--green-transition);
  }
}
.button__icon {
  width: 1rem;
  height: 1rem;
  fill: var(--white);
}

.authorization {
  height: 100dvh;
  position: relative;
  display: flex;
}
.authorization__left-side {
  display: flex;
  flex-direction: column;
  justify-content: flex-end;
  position: relative;
  flex: 0 0 63%;
}
@media (max-width: 80em) {
  .authorization__left-side {
    flex: 0 0 55%;
  }
}
@media (max-width: 64em) {
  .authorization__left-side {
    display: none;
  }
}
.authorization__left-side::before {
  position: absolute;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  content: "";
  background-image: url("../img/auth.png");
  background-image: image-set(url("../img/auth@2x.webp") type("image/webp") 2x, url("../img/auth.webp") type("image/webp") 1x, url("../img/auth@2x.png") type("image/png") 2x, url("../img/auth.png") type("image/png") 1x);
  background-size: cover;
  background-position: center;
  background-repeat: no-repeat;
}
.authorization__content {
  overflow: auto;
  padding: 1.25rem;
  flex: 1 1 auto;
}
.authorization__inset {
  margin: 0 auto;
  width: 100%;
  max-width: 23.75rem;
  min-height: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
}
.authorization__header {
  margin-bottom: 3.125rem;
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  align-items: center;
  gap: 1.25rem 0.9375rem;
}
@media (max-width: 80em) {
  .authorization__header {
    margin-bottom: 2.5rem;
  }
}
@media (max-width: 40em) {
  .authorization__header {
    justify-content: center;
  }
}
.authorization__logo {
  flex-shrink: 0;
  width: 9.25rem;
  height: 1.9375rem;
}
.authorization__logo-icon {
  width: 9.25rem;
  height: 1.9375rem;
}
.authorization__login {
  font-size: 0.875rem;
  color: #8899a8;
  text-align: right;
}
.authorization__link-login {
  text-decoration: underline;
  text-decoration-skip-ink: none;
  color: var(--green);
}
@media (any-hover: hover) {
  .authorization__link-login:hover {
    text-decoration: none;
  }
}
.authorization__title {
  margin-bottom: 1rem;
  font-family: var(--second-family);
  font-size: 2rem;
  font-weight: 600;
}
@media (max-width: 80em) {
  .authorization__title {
    font-size: 1.75rem;
    margin-bottom: 0.75rem;
  }
}
.authorization__text {
  margin-bottom: 1.75rem;
  line-height: 140%;
  font-size: 0.875rem;
  color: #8899a8;
}
@media (max-width: 80em) {
  .authorization__text {
    margin-bottom: 1.25rem;
  }
}
.authorization__tabs {
  margin-bottom: 1.25rem;
  width: 100%;
  min-height: 1.875rem;
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  border: 1px solid var(--grey-border);
  border-radius: 5px;
  overflow: hidden;
}
.authorization__tab {
  height: 100%;
  font-weight: 500;
  font-size: 0.8125rem;
  display: flex;
  align-items: center;
  background: #f3f4f6;
  justify-content: center;
  transition: color 0.3s ease, background 0.3s ease;
}
.authorization__tab.active {
  background-color: var(--green);
  color: var(--white);
  pointer-events: none;
}
@media (any-hover: hover) {
  .authorization__tab:hover {
    background-color: var(--green);
    color: var(--white);
  }
}
.authorization__items:not(:last-child) {
  margin-bottom: 1.4375rem;
}
@media (max-width: 80em) {
  .authorization__items:not(:last-child) {
    margin-bottom: 0.9375rem;
  }
}
.authorization__privacy {
  font-size: 0.6875rem;
  color: #8899a8;
  line-height: 140%;
}
.authorization__privacy a {
  text-decoration: underline;
  text-decoration-skip-ink: none;
}
@media (any-hover: hover) {
  .authorization__privacy a:hover {
    text-decoration: none;
  }
}
.authorization__button {
  min-height: 3.125rem;
  width: 100%;
}
.authorization__button:not(:last-child) {
  margin-bottom: 1.875rem;
}
@media (max-width: 80em) {
  .authorization__button:not(:last-child) {
    margin-bottom: 1.25rem;
  }
}
@media (max-width: 62em) {
  .authorization__button {
    min-height: 2.5rem;
  }
}
.authorization__socials-text {
  margin-bottom: 0.875rem;
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 0.625rem;
  position: relative;
  text-wrap: nowrap;
  font-size: 0.875rem;
  line-height: 171%;
  text-align: center;
  color: #8899a8;
}
.authorization__socials-text::after {
  content: "";
  background-color: var(--grey-border);
  height: 0.0625rem;
  width: 100%;
}
.authorization__socials-text::before {
  content: "";
  background-color: var(--grey-border);
  height: 0.0625rem;
  width: 100%;
}
.authorization__socials-items {
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 1.25rem;
}
.authorization__socials-link {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 2.8125rem;
  height: 2.8125rem;
  border-radius: 16px;
  background-color: var(--white);
  border: 1px solid var(--grey-border);
  transition: background 0.3s ease 0s;
}
@media (any-hover: hover) {
  .authorization__socials-link:hover {
    background-color: var(--grey-hover);
  }
}
.authorization__socials-link-icon {
  width: 1.5rem;
  height: 1.5rem;
}

.header {
  position: absolute;
  top: 0;
  width: 100%;
  z-index: 100;
}
@media (min-width: 62.0625em) {
  .header {
    transform: translate(0px, -1.875rem);
    opacity: 0;
    transition: transform 1.4s ease, opacity 1.4s ease;
  }
  .header.loaded {
    transform: translate(0px, 0);
    opacity: 1;
  }
}
.header--page {
  position: relative;
}
.header__container {
  padding-top: 1.5625rem;
  padding-bottom: 1.5625rem;
  display: flex;
  align-items: center;
  justify-content: space-between;
  gap: 0.4375rem 1.25rem;
}
@media (max-width: 62em) {
  .header__container {
    padding-top: 0.9375rem;
    padding-bottom: 0.9375rem;
    gap: 0.4375rem 0.9375rem;
  }
}
@media (max-width: 40em) {
  .header__container {
    gap: 0.4375rem 0.625rem;
  }
}
.header__logo {
  position: relative;
  z-index: 5;
  width: 11.5rem;
  height: 2.4375rem;
}
@media (max-width: 40em) {
  .header__logo {
    width: 9.375rem;
  }
}
.header__logo-icon {
  width: 100%;
  height: 100%;
}
.header__menu {
  flex-grow: 1;
}
.header__actions {
  display: flex;
  align-items: center;
  margin-left: auto;
  gap: 0.625rem 0.125rem;
}
.header__action {
  padding: 0.25rem 1.5rem;
  min-height: 2.5rem;
  text-wrap: nowrap;
}
@media (max-width: 48em) {
  .header__action {
    padding: 0.25rem 0.625rem;
  }
}

.menu__body {
  display: flex;
  align-items: stretch;
  gap: 0.625rem 1.25rem;
  min-height: 2.5rem;
}
@media (max-width: 62em) {
  .menu__body {
    padding: 7.5rem 0.9375rem 1.5625rem 0.9375rem;
    gap: 1.875rem 1.25rem;
    flex-direction: column;
    align-items: start;
    position: fixed;
    width: 100%;
    height: 100%;
    overflow: auto;
    top: 0;
    left: -100%;
    background-color: var(--white);
    transition: left 0.3s ease;
  }
  .menu__body::before {
    content: "";
    position: fixed;
    width: 100%;
    height: 3.4375rem;
    top: 0;
    left: -100%;
    transition: left 0.3s ease 0s;
    background-color: rgba(255, 255, 255, 0.75);
    z-index: 2;
  }
  .menu__body._active {
    left: 0;
  }
  .menu__body._active::before {
    left: 0;
  }
}
.menu__list {
  flex-grow: 1;
}
@media (min-width: 62.0625em) {
  .menu__list {
    display: flex;
    column-gap: 3.75rem;
    row-gap: 0.3125rem;
    flex-wrap: wrap;
    justify-content: center;
  }
}
@media (max-width: 64em) {
  .menu__list {
    column-gap: 1.875rem;
  }
}
@media (max-width: 62em) {
  .menu__list {
    width: 100%;
    display: flex;
    flex-direction: column;
    row-gap: 1.25rem;
  }
}
.menu__item {
  display: flex;
  align-items: center;
  justify-content: space-between;
  flex-wrap: wrap;
  position: relative;
}
@media (any-hover: hover) {
  .menu__item:hover .menu__link {
    color: var(--green);
  }
  .menu__item:hover .menu__arrow-icon {
    stroke: var(--green);
  }
}
@media (any-hover: hover) {
  .menu__item:hover .menu__sub-list {
    transform: translate(0, 0);
    opacity: 1;
    visibility: visible;
    pointer-events: auto;
  }
  .menu__item:hover .menu__arrow-icon {
    transform: rotate(-180deg);
    stroke: var(--green);
  }
  .menu__item:hover .menu__link {
    color: var(--green);
  }
}
.menu__link {
  display: flex;
  align-items: center;
  gap: 0.4375rem;
  font-weight: 500;
  font-size: 0.875rem;
  transition: color 0.3s ease;
}
.menu__link:focus-visible {
  outline: 2px solid var(--green);
}
@media (max-width: 62em) {
  .menu__link {
    font-size: 1.5rem;
    justify-content: space-between;
    width: 100%;
  }
}
@media (max-width: 62em) {
  ._init .menu__link._active .menu__arrow-icon {
    transform: rotate(-180deg);
    stroke: var(--green);
  }
}
.menu__arrow-icon {
  pointer-events: none;
  width: 0.75rem;
  height: 0.375rem;
  fill: none;
  stroke: var(--main-color);
  transition: stroke 0.3s ease, transform 0.3s ease;
}
@media (max-width: 62em) {
  .menu__arrow-icon {
    width: 1.25rem;
    height: 1rem;
  }
}
.menu__sub-list {
  padding: 0.9375rem;
  position: relative;
  background: var(--green);
  flex: 1 1 100%;
}
@media (min-width: 62.0625em) {
  .menu__sub-list {
    padding: 0.625rem;
    pointer-events: none;
    min-width: 12.5rem;
    position: absolute;
    left: 0;
    top: 100%;
    z-index: 5;
    border-radius: 8px;
    opacity: 0;
    transform: translate(0, 0.625rem);
    visibility: hidden;
    transition: opacity 0.3s ease, transform 0.3s ease;
  }
}
@media (max-width: 62em) {
  .menu__sub-list {
    margin: 0.625rem -0.9375rem 0 -0.9375rem;
  }
}
.menu__sub-item:not(:last-child) {
  margin: 0 0 0.3125rem 0;
}
@media (max-width: 62em) {
  .menu__sub-item:not(:last-child) {
    margin: 0 0 0.625rem 0;
  }
}
.menu__sub-link {
  color: var(--white);
  line-height: 120%;
  font-size: 0.8125rem;
}
@media (any-hover: hover) {
  .menu__sub-link:hover {
