@use '_mixin' as m;

.hero {
  position: relative;
  padding-top: m.toRem(90);
  padding-bottom: m.toRem(90);
  height: 100dvh;
  display: grid;
  align-items: center;
  background: url('../img/svg/hero.svg') center bottom / cover no-repeat;

  @media (max-width: m.toEm(1024)) {
    padding-top: m.toRem(110);
    padding-bottom: m.toRem(90);
    // height: auto;
  }

  @media (max-width: m.toEm(640)) {
    padding-top: m.toRem(100);
    padding-bottom: m.toRem(120);
  }

  &__container {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: stretch;
    width: 100%;
    gap: m.toRem(30);

  }

  &__header {
  }

  // .hero__search

  &__search {
    margin: 0 auto;
    padding: m.toRem(20) m.toRem(20);
    display: flex;
    align-items: stretch;
    gap: m.toRem(12);
    width: 100%;
    max-width: m.toRem(643);
    min-height: m.toRem(90);
    box-shadow: 0 5px 12px 0 rgba(0, 0, 0, 0.1);
    background: var(--white);
    border-radius: 12px;

    @media (max-width: m.toEm(1280)) {
      padding: m.toRem(15) m.toRem(15);
      min-height: m.toRem(75);
    }

    @media (max-width: m.toEm(640)) {
      flex-direction: column;
    
      width: 100%;
      padding: m.toRem(15) m.toRem(15);
      min-height: m.toRem(75);
    }
  }

  // .hero__search-input

  &__search-input {
    padding: m.toRem(12) m.toRem(20);
    width: 100%;
    box-shadow: 0 0 0 1px var(--grey-border);
    border-radius: 6px;
    transition: box-shadow 0.3s ease;
    outline: none;
    font-size: m.toRem(14);

    &:focus {
      outline: none;
      box-shadow: 0 0 0 2px var(--green);
    }

    &::placeholder {
      color: var(--grey-light);
    }

    @media (max-width: m.toEm(640)){
        width: auto;
    }
  }

  // .hero__search-button

  &__search-button {
    min-width: m.toRem(132);
    font-size: m.toRem(16);
    min-height: m.toRem(45);
  }

  &__search-button-icon {
    width: m.toRem(16);
    height: m.toRem(16);
  }
}
