@use '_mixin' as m; // Блок со спойлерами

.faq {
  padding-top: m.toRem(146);
  padding-bottom: m.toRem(140);

  background: url('../img/svg/faq.svg') center bottom / contain no-repeat;

  @media (max-width: m.toEm(1280)) {
    padding-top: m.toRem(100);
    padding-bottom: m.toRem(100);
  }

  @media (max-width: m.toEm(640)) {
    padding-top: m.toRem(80);
    padding-bottom: m.toRem(70);
  }

  &__header {
    margin-bottom: m.toRem(70);

    @media (max-width: m.toEm(640)) {
      margin-bottom: m.toRem(40);
    }
  }
}

.spollers-faq {
  display: grid;
  grid-template-columns: repeat(2, minmax(m.toRem(350), m.toRem(570)));
  justify-content: space-between;
  gap: m.toRem(10);

  @media (max-width: m.toEm(992)) {
    grid-template-columns: 1fr;
    gap: m.toRem(15);
  }
}

// Спойлер
.spollers-faq__item {
  padding-bottom: m.toRem(10);

  border: 1px solid #f3f4fe;
  border-radius: 10px;
  box-shadow: 0 20px 95px 0 rgba(201, 203, 204, 0.3);
  background: var(--white);
  overflow: hidden;
  &:not(:last-child) {
    margin-bottom: m.toRem(30);

    @media (max-width: m.toEm(992)) {
      margin-bottom: m.toRem(15);
    }
  }
}

// Заголовок спойлера
.spollers-faq__title {
  position: relative;
  padding: m.toRem(20) m.toRem(30) m.toRem(10) m.toRem(100);
  min-height: m.toRem(95);
  display: flex;
  justify-content: space-between;
  align-items: center;
  gap: m.toRem(20);
  width: 100%;
  cursor: default;
  text-align: left;
  background: transparent;
  font-size: m.toRem(18);
  font-weight: 700;
  line-height: 120%;

  @media (max-width: m.toEm(992)) {
    padding: m.toRem(20) m.toRem(70) m.toRem(10) m.toRem(20);
    min-height: m.toRem(75);
  }

  @media (max-width: m.toEm(640)) {
    padding: m.toRem(20) m.toRem(60) m.toRem(10) m.toRem(20);
    font-size: m.toRem(16);
  }

  ._init & {
    cursor: pointer;

    &._active {
      .spollers-faq__icon {
        transform: rotate(-180deg);
      }
    }
  }

  &:focus-visible {
    outline: none;

    .spollers-faq__arrow {
      outline: 2px solid var(--green-transition);
    }
  }
}

.spollers-faq__arrow {
  position: absolute;
  left: m.toRem(30);
  top: m.toRem(32);
  width: m.toRem(42);
  height: m.toRem(42);
  display: flex;
  align-items: center;
  justify-content: center;
  border-radius: 10px;
  background: rgba(19, 194, 150, 0.06);

  @media (max-width: m.toEm(992)) {
    left: auto;
    right: m.toRem(18);
    top: m.toRem(22);
  }

  @media (max-width: m.toEm(640)) {
    right: m.toRem(10);
    width: m.toRem(35);
    height: m.toRem(35);
  }
}

.spollers-faq__icon {
  fill: var(--green);
  width: m.toRem(18);
  height: m.toRem(10);
  transition: transform 0.2s ease, fill 0.2s ease;

  @media (max-width: m.toEm(640)) {
    width: m.toRem(16);
    height: m.toRem(8);
  }
}

// Контент спойлера
.spollers-faq__body {
  padding: m.toRem(0) m.toRem(30) m.toRem(40) m.toRem(100);

  @media (max-width: m.toEm(992)) {
    padding: m.toRem(0) m.toRem(70) m.toRem(20) m.toRem(20);
  }

  @media (max-width: m.toEm(640)) {
    padding: m.toRem(0) m.toRem(50) m.toRem(20) m.toRem(20);
  }
}
