@use '_mixin' as m;

.authorization {
  height: 100dvh;
  position: relative;
  display: flex;

  &__left-side {
    display: flex;
    flex-direction: column;
    justify-content: flex-end;
    position: relative;
    flex: 0 0 63%;

    @media (max-width: m.toEm(1280)) {
      flex: 0 0 55%;
    }

    @media (max-width: m.toEm(1024)) {
      display: none;
    }

    &::before {
      position: absolute;
      width: 100%;
      height: 100%;
      top: 0;
      left: 0;
      content: '';
      background-image: url('../img/auth.png');
      background-image: image-set(
        url('../img/auth@2x.webp') type('image/webp') 2x,
        url('../img/auth.webp') type('image/webp') 1x,
        url('../img/auth@2x.png') type('image/png') 2x,
        url('../img/auth.png') type('image/png') 1x
      );
      background-size: cover;
      background-position: center;
      background-repeat: no-repeat;
    }
  }

  &__content {
    overflow: auto;
    padding: m.toRem(20);
    flex: 1 1 auto;
  }

  &__inset {
    margin: 0 auto;
    width: 100%;
    max-width: m.toRem(380);
    min-height: 100%;
    display: flex;

    flex-direction: column;
    justify-content: center;
  }

  &__header {
    margin-bottom: m.toRem(50);
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    align-items: center;
    gap: m.toRem(20) m.toRem(15);

    @media (max-width: m.toEm(1280)) {
      margin-bottom: m.toRem(40);
    }
    @media (max-width: m.toEm(640)) {
      justify-content: center;
    }
  }

  &__logo {
    flex-shrink: 0;
    width: m.toRem(148);
    height: m.toRem(31);
  }

  &__logo-icon {
    width: m.toRem(148);
    height: m.toRem(31);
  }

  &__login {
    font-size: m.toRem(14);
    color: #8899a8;
    text-align: right;
  }

  &__link-login {
    text-decoration: underline;
    text-decoration-skip-ink: none;
    color: var(--green);

    @media (any-hover: hover) {
      &:hover {
        text-decoration: none;
      }
    }
  }

  &__title {
    margin-bottom: m.toRem(16);
    font-family: var(--second-family);
    font-size: m.toRem(32);
    font-weight: 600;

    @media (max-width: m.toEm(1280)) {
      font-size: m.toRem(28);
      margin-bottom: m.toRem(12);
    }
  }

  &__text {
    margin-bottom: m.toRem(28);
    line-height: 140%;
    font-size: m.toRem(14);
    color: #8899a8;

    @media (max-width: m.toEm(1280)) {
      margin-bottom: m.toRem(20);
    }
  }

  &__tabs {
    margin-bottom: m.toRem(20);
    width: 100%;
    min-height: m.toRem(30);
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    border: 1px solid var(--grey-border);
    border-radius: 5px;
    overflow: hidden;
  }

  &__tab {
    height: 100%;
    font-weight: 500;
    font-size: m.toRem(13);
    display: flex;
    align-items: center;
    background: #f3f4f6;
    justify-content: center;
    transition: color 0.3s ease, background 0.3s ease;

    &.active {
      background-color: var(--green);
      color: var(--white);
      pointer-events: none;
    }
    @media (any-hover: hover) {
      &:hover {
        background-color: var(--green);
        color: var(--white);
      }
    }
  }

  &__items {
    &:not(:last-child) {
      margin-bottom: m.toRem(23);
    }

    @media (max-width: m.toEm(1280)) {
      &:not(:last-child) {
        margin-bottom: m.toRem(15);
      }
    }
  }

  &__privacy {
    font-size: m.toRem(11);
    color: #8899a8;
    line-height: 140%;
    a {
      text-decoration: underline;
      text-decoration-skip-ink: none;
      @media (any-hover: hover) {
        &:hover {
          text-decoration: none;
        }
      }
    }
  }

  &__button {
    &:not(:last-child) {
      margin-bottom: m.toRem(30);
    }

    min-height: m.toRem(50);
    width: 100%;

    @media (max-width: m.toEm(1280)) {
      &:not(:last-child) {
        margin-bottom: m.toRem(20);
      }
    }

    @media (max-width: m.toEm(992)) {
      min-height: m.toRem(40);
    }
  }

  &__socials-text {
    margin-bottom: m.toRem(14);
    display: flex;
    align-items: center;
    justify-content: center;
    gap: m.toRem(10);
    position: relative;
    text-wrap: nowrap;
    font-size: m.toRem(14);
    line-height: 171%;
    text-align: center;
    color: #8899a8;

    &::after {
      content: '';
      background-color: var(--grey-border);
      height: m.toRem(1);
      width: 100%;
    }

    &::before {
      content: '';
      background-color: var(--grey-border);
      height: m.toRem(1);
      width: 100%;
    }
  }

  &__socials-items {
    display: flex;
    align-items: center;
    justify-content: center;
    gap: m.toRem(20);
  }

  &__socials-link {
    display: flex;
    align-items: center;
    justify-content: center;
    width: m.toRem(45);
    height: m.toRem(45);
    border-radius: 16px;
    background-color: var(--white);
    border: 1px solid var(--grey-border);
    transition: background 0.3s ease 0s;

    @media (any-hover: hover) {
      &:hover {
        background-color: var(--grey-hover);
      }
    }
  }
  &__socials-link-icon {
    width: m.toRem(24);
    height: m.toRem(24);
  }
}
