@use '_mixin'as m;

.form * {
  outline: none;
}

input:-webkit-autofill {
  -webkit-box-shadow: 0 0 0 m.toRem(50) #fff inset;
}

.form {
  &__items {
    display: flex;
    flex-direction: column;
    gap: m.toRem(22);

    @media (max-width: m.toEm(1280)) {
      gap: m.toRem(12);
    }
  }

  &__item {
    display: flex;
    flex-direction: column;
    flex-grow: 1;
    gap: m.toRem(6);

    &.error {
      .input__control {
        box-shadow: 0 0 0 1px var(--red);
      }

      .error-text {
        display: block;
      }
    }
  }

  &__label {
    font-weight: 600;
    font-size: m.toRem(14);
  }

  &__row {}

  &__actions {
    align-self: end;
    display: flex;
    justify-content: end;
    gap: m.toRem(16);

    .button {
      width: m.toRem(160);
      min-height: m.toRem(36);
    }
  }

  &__item-actions {
    display: flex;
    gap: m.toRem(10);
  }
}

.input {
  position: relative;

  &__control {
    padding-left: m.toRem(20);
    padding-right: m.toRem(40);
    display: flex;
    align-items: center;
    width: 100%;

    height: m.toRem(46);
    font-weight: 500;

    border-radius: 6px;
    font-size: m.toRem(14);
    box-shadow: 0 0 0 1px var(--grey-border);
    overflow: hidden;
    transition: box-shadow 0.3s ease;

    @media (max-width: m.toEm(992)) {
      height: m.toRem(40);
    }

    &:focus {
      outline: none;
      box-shadow: 0 0 0 1px var(--green);
    }

    &--select {
      height: m.toRem(40);
      padding-left: m.toRem(15);
      padding-right: m.toRem(30);
      overflow: hidden;
      white-space: nowrap;
      text-overflow: ellipsis;
    }

    &.dropdown-input {
      cursor: pointer;
    }

    &--textarea {
      padding: m.toRem(20) m.toRem(20);
      resize: none;
      height: m.toRem(144);
      width: 100%;
    }

    &::placeholder {
      color: var(--grey-light);
    }
  }

  &__btn {
    position: absolute;
    right: m.toRem(15);
    bottom: 50%;
    transform: translate(0px, 50%);
    display: flex;
    align-items: center;
    justify-content: center;
  }

  &__btn-arrow {
    transition: transform 0.3s ease 0s;
    width: m.toRem(10);
    height: m.toRem(5);
  }

  &__btn-calendar {
    width: m.toRem(24);
    height: m.toRem(24);
  }

  &__btn-eye {
    width: m.toRem(16);
    height: m.toRem(16);
    fill: var(--green);

    .show &.on {
      display: block;
    }

    .show &.off {
      display: none;
    }

    &.on {
      display: none;
    }

    &.off {}

    &.show {}
  }

  &__icon {
    position: absolute;
    left: m.toRem(20);
    bottom: 50%;
    transform: translate(0px, 50%);
    width: m.toRem(24);
    height: m.toRem(24);
  }

  // .input__text

  &__text {
    position: absolute;
    right: m.toRem(20);
    bottom: 50%;
    transform: translate(0px, 50%);
    font-weight: 500;
    font-size: m.toRem(14);
  }

  &.show {
    .input__control {
      outline: none;
    }

    .dropdown-select {
      opacity: 1;
      visibility: visible;
      transform: translate(0, 0);
    }

    .input__btn-arrow {
      transform: rotate(-180deg);
    }
  }
}

.dropdown-select {
  padding: m.toRem(5) m.toRem(0);
  position: absolute;
  width: max-content;
  min-width: 100%;
  z-index: 100;
  display: flex;
  flex-direction: column;
  gap: m.toRem(2);
  top: calc(100% + m.toRem(5));
  right: 0;
  box-shadow: 0 1px 3px 0 rgba(166, 175, 195, 0.6);
  background: var(--white);
  border-radius: 4px;
  opacity: 0;
  visibility: hidden;
  font-size: m.toRem(14);

  transform: translate(0, m.toRem(-10));
  transition: opacity 0.3s ease;

  &__item {
    padding: m.toRem(12) m.toRem(15);
    display: flex;
    align-items: center;
    gap: m.toRem(10);
    transition: background 0.3s ease 0s;
    cursor: pointer;
    -webkit-tap-highlight-color: transparent;
  }

  &__icon {
    width: m.toRem(24);
    height: m.toRem(24);
  }
}

.error-text {
  margin-top: m.toRem(2);
  display: none;
  font-size: m.toRem(11);
  color: red;
}