@use '_mixin' as m;
.filter-overlay {
  position: fixed;
  top: 0;
  left: 0;
  width: 100vw;
  height: 100vh;
  background-color: rgba(0, 0, 0, 0.5);
  opacity: 0;
  pointer-events: none;
  transition: opacity 0.3s ease;

  z-index: 999;
  &._active {
    opacity: 1;
    pointer-events: auto;
  }
}
.filter-reviews {
  position: relative;

  &__close {
    display: none;
    @media (max-width: m.toEm(992)) {
      position: absolute;
      display: block;
      top: m.toRem(10);
      right: m.toRem(10);
    }
  }

  &__close-icon {
      width: m.toRem(16);
      height: m.toRem(16);
  }
  @media (max-width: m.toEm(992)) {
    padding: m.toRem(40) m.toRem(15);
    position: fixed;
    top: 0;
    left: -100%;
    background-color: #fff;

    height: 100%;
    transition: left 0.3s ease;
    z-index: 1001;
    overflow: auto;

    &._active {
      left: 0;
    }
  }

  // .filter-reviews__items

  &__items {
    display: flex;
    flex-direction: column;
    gap: m.toRem(20);
  }

  // .filter-reviews__item

  &__item {
    display: flex;
    flex-direction: column;
    border: 1px solid #dfe4ea;
    border-radius: 8px;
  }

  // .filter-reviews__item-title

  &__item-title {
    padding: m.toRem(15) m.toRem(30) m.toRem(10);
    border-bottom: 1px solid var(--grey-border);
    font-weight: 600;
  }

  // .filter-reviews__item-body

  &__item-body {
    padding: m.toRem(15) m.toRem(30);
  }

  // .filter-reviews__rating

  &__rating {
  }
}

.categories-filter {
  display: flex;
  flex-direction: column;
  gap: m.toRem(12);

  // .categories-filter__item

  &__item {
    margin: 0 m.toRem(-30);
    padding: 0 m.toRem(30);
    position: relative;
    min-height: m.toRem(29);
    display: flex;
    align-items: center;
    justify-content: space-between;
    gap: m.toRem(5);
    color: var(--text);
    font-size: m.toRem(14);
    @media (any-hover: hover) {
      &:hover {
        background-color: var(--grey-hover);
      }
    }
  }

  // .categories-filter__link

  &__link {
    position: absolute;
    width: 100%;
    height: 100%;
    top: 0;
    left: 0;
  }

  // .categories-filter__text

  &__text {
  }

  // .categories-filter__amount

  &__amount {
    text-align: end;
  }
}
